ndroidVersion))+"</small>"):"<!---->")+"</a>"):"<!---->")+"</div></div> <div class=\"download-preview\"><img src=\"/1.png\" alt=\"手机预览\" class=\"phone-preview\"></div></div></div> <div class=\"opensource-section\"><h2 class=\"section-title\">原木社区由 OpenLoghome 开源项目驱动</h2> <div class=\"opensource-container\"><div class=\"opensource-hero\"><h3 class=\"opensource-title\">&quot; 将社区的建设权，交还给所有人 &quot;</h3> <div class=\"opensource-intro\"><div class=\"intro-image\"><img src=\"/图片1.png\" alt=\"开源计划\" class=\"intro-img\"></div> <div class=\"intro-content\"><p>亲爱的朋友，</p> <p>原木社区 是一款为 Minecraft 及其衍生文化爱好者打造的综合性文学作品平台，涵盖作品创作与阅读体验。自2021年下半年开始，我们已经持续稳定地运营了两年多时间。</p> <p>而在今天，2024年3月1日，我们将<strong>做一件大胆的事</strong>，这标志着原木社区进入新篇章，我们将一起奔跑、一起努力、一起见证、一起参与这个社区的成长与发展。</p></div></div></div> <div class=\"opensource-content\"><div class=\"content-block\"><p>Minecraft 同人文学作品是一个小众但活跃的社群，我们在热爱Minecraft的同时也热爱文学，拥有独特的品味和风趣。</p> <p>原木社区自启动以来，已经积累了超<strong>200位用户</strong>，周活跃用户数达<strong>81人</strong>，这对于一个小众的群体来说难能可贵。</p> <p>但在社区蓬勃发展同时，我们<strong>一直面临着人力短缺的挑战</strong>。作为一个公益性社区，我们一直以来一直在努力维护开发、维护和运营的平衡。我们不仅投入资金和大量精力来维护服务器的运行，还积极收集用户反馈，并进行详尽的分析和实现。</p> <p>随着需求的增长和系统复杂度的提升，我们很难集中精力为原木社区引入新的大型功能，因此开发工作也逐渐停滞不前。</p> <p>然而，我们幸运地拥有一个活跃的用户社区。<strong>在深思熟虑之后，我们决定将社区建设的权利交给大家，启动《原木社区开源计划》。</strong></p> <p>在该计划下，原木社区现有的<strong>全部技术资产</strong>（包括前端和后端代码，但不包括用户数据）将全部以Mozilla公共许可证<a href=\"https://github.com/OpenLogHome\" target=\"_blank\">开源至GitHub</a>，并欢迎社区成员的贡献。每一行代码、每一个提交的问题、每一个报告的bug，甚至每一个提出的问题，都将对原木社区的发展产生重要影响。</p> <p>与此同时，我们将继续<strong>坚持推进原木社区的官方版本的运营工作</strong>。通过广告和原木充值等收入维持服务器的正常运行，保障大家熟悉的社区万古长青。</p></div> <div class=\"participation-section\"><div class=\"participation-image\"><img src=\"/teamwork.png\" alt=\"团队合作\" class=\"teamwork-img\"></div> <div class=\"participation-content\"><p>无论是贡献代码、添加 API 文档、整理 bugs 和 issues、review 代码……</p> <p>原木社区 在方方面面都期待你的助力。</p> <p>你可以根据你自身的情况，选择以下的一项或者多项，参与到 原木社区 的开源项目中来：</p> <ul class=\"participation-list\"><li>贡献代码</li> <li>建设 API 文档</li> <li>Review PR</li> <li>向有需要的人推荐 原木社区</li> <li>参与 原木社区 的群讨论</li></ul> <p>…… 所有你能想到的让 原木社区 变得更好的事情</p></div></div> <div class=\"github-section\"><div class=\"github-image\"><img src=\"/github.png\" alt=\"GitHub\" class=\"github-img\"></div> <div class=\"github-content\"><p>我们的主要开源部分有：</p> <p><strong>loghome</strong>：包含 原木社区 的完整技术代码</p> <p><strong>loghome-testDb</strong>：包含用于社区功能测试的完整数据库</p> <p>你也可以在 <a href=\"https://github.com/OpenLogHome\" target=\"_blank\">这里</a> 找到更多的 原木社区 开源项目。</p> <div class=\"contribution-guide\"><h4>代码贡献</h4> <p>对于代码贡献，我们需要你<strong>明确说明修改了哪些功能</strong>，并在贡献前<strong>确保业务功能经过测试</strong>。</p> <p>由于原木社区是一个前后端分离的项目，且还在线上运营，因此请<strong>尽少可能变动数据库的结构</strong>，以保障用户的数据安全。如果你增加了新的表或字段，也一定要<strong>明确说明</strong>。</p> <h4>API文档贡献</h4> <p>原木社区 的 API 文档使用 ApiDoc 创建。 API 文档直接撰写在后端项目代码行间，统一放在接口声明函数的前面。</p> <p>如果你在贡献中修改了API文档，请<strong>明确说明</strong>。</p></div></div></div> <div class=\"community-section\"><div class=\"community-image\"><img src=\"/message.png\" alt=\"社区交流\" class=\"message-img\"></div> <div class=\"community-content\"><p>欢迎各位参与到原木社区活跃的开发中，在社群中碰撞出思想的火花。</p> <p><strong>QQ群：701928273</strong></p> <div class=\"join-buttons\"><a href=\"https://github.com/OpenLogHome\" target=\"_blank\" class=\"join-btn github-btn\">\n                访问 GitHub\n              </a> <a href=\"tencent://groupwpa/?subcmd=all&param=7B2267726F757055696E223A3730313932383237332C2274696D655374616D70223A313733353030303030307D0A\" class=\"join-btn qq-btn\">\n                加入 QQ 群\n              </a></div></div></div></div></div></div>")],2)
